// wrapper.js
import { createWrapper, resolveWhen } from '@segment/analytics-consent-tools'

/**
 * Normalizes the Osano consent object to the format Segment expects.
 * Osano format: { MARKETING: "ACCEPT", ANALYTICS: "DENY", ... }
 * Segment format: { Advertising: true, Analytics: false, ... } // Uses Segment standard categories
 *
 * !!! IMPORTANT !!!
 * Customize the categoryMap and finalSegmentCategories structure below
 * to match your specific Osano categories and Segment Consent Management setup.
 */
const normalizeOsanoConsent = (osanoConsent) => {
  const segmentCategories = {};
  if (!osanoConsent) {
    console.warn('normalizeOsanoConsent received null or undefined osanoConsent');
    return {};
  }

  // 1. Map Osano technical categories to intermediate keys (e.g., lowercase)
  //    >>> Adjust this map keys to match your Osano Category names <<<
  const categoryMap = {
    'ESSENTIAL': 'essential',
    'ANALYTICS': 'analytics',
    'MARKETING': 'marketing',
    'PERSONALIZATION': 'personalization',
    'STORAGE': 'storage',
    // Add any other categories from your Osano configuration here
		'OPT_OUT': 'opt_out'
  };

  for (const [osanoCategory, value] of Object.entries(osanoConsent)) {
    const mappedKey = categoryMap[osanoCategory];
    if (mappedKey) {
      // Convert 'ACCEPT'/'DENY' to true/false
      segmentCategories[mappedKey] = value === 'ACCEPT';
    } else {
      console.warn(`Osano category "${osanoCategory}" not found in categoryMap.`);
    }
  }

  // 2. Map intermediate keys to final Segment standard consent categories
  //    >>> Adjust these mappings based on your Segment Consent settings <<<
  //    The keys ('Advertising', 'Analytics', 'Functional') should match
  //    what Segment expects for its consent enforcement.
  const finalSegmentCategories = {
    'Advertising': segmentCategories.marketing,
    'Analytics': segmentCategories.analytics,
    'Functional': segmentCategories.personalization || segmentCategories.storage || segmentCategories.essential,
    // Example: Add 'ID Sync' if you map an Osano category to it
    // 'ID Sync': segmentCategories.someOtherOsanoKey,
  };

   // Remove keys that ended up with undefined values (if a category wasn't present or mapped)
  Object.keys(finalSegmentCategories).forEach(key => {
    if (finalSegmentCategories[key] === undefined) {
      delete finalSegmentCategories[key];
    }
  });

  console.log('Normalized Osano Consent for Segment:', finalSegmentCategories);
  return finalSegmentCategories;
};

export const withCMP = createWrapper({
  // Wait to load wrapper or call "shouldLoadSegment" until window.CMP exists.
  shouldLoadWrapper: async () => {
    // await resolveWhen(() => window.CMP !== undefined, 500)

		// Wait for window.Osano.cm and necessary functions to exist
		await resolveWhen(
			() => window.Osano && window.Osano.cm.getConsent !== undefined && window.Osano.cm.addEventListener !== undefined,
			500 // Check interval in milliseconds
		);
		console.log('Osano SDK detected by Segment wrapper.');
	},

  // Allow for control over wrapper + analytics initialization.
  // Delay any calls to analytics.load() until this function returns / resolves.
  /**
   * Decide if/when Segment's analytics.js should load.
   * Use the 'osano-cm-initialized' event for efficiency.
   */
	shouldLoadSegment: async (ctx) => {
    /*
    // Optional -- for granular control of initialization
    if (noConsentNeeded) {
      ctx.abort({ loadSegmentNormally: true })
    } else if (allTrackingDisabled) {
      ctx.abort({ loadSegmentNormally: false })
    }
    */
    // if (window.CMP.ConsentModel === 'opt-out') {
    //   return ctx.load({ consentModel: 'opt-out' })
    // } else {
    //   await resolveWhen(
    //     () => !window.CMP.popUpVisible() && window.CMP.categories.length,
    //     500
    //   )
    //   return ctx.load({ consentModel: 'opt-in' })
    // }
    console.log('Segment wrapper: Waiting for Osano CMP initialization...');
    return new Promise((resolve) => {
       // Check if Osano object exists before adding listener
       if (!window.Osano.cm) {
           console.error("Segment wrapper: Osano object (window.Osano.cm) not found. Cannot add initialization listener. Aborting Segment load.");
           // Abort loading Segment if Osano is missing entirely
           ctx.abort({ loadSegmentNormally: false });
           resolve(undefined); // Resolve promise even on abort
           return;
       }

      let resolved = false; // Flag to track if loading has resolved/timed out

      const listener = (initialConsent) => {
        if (resolved) return; // Prevent running if timeout already occurred
        resolved = true;
        console.log('Segment wrapper: Osano CMP initialized event received.');
        // Proceed to load Segment; initial consent will be fetched by getCategories
        resolve(ctx.load());
      };

      // Add the listener for Osano initialization
      // This relies on Osano's documented behavior that the callback
      // runs immediately if the event has already fired before the listener was added.
      window.Osano.cm.addEventListener('osano-cm-initialized', listener);

      // Safeguard: Timeout if the initialization event doesn't fire
      setTimeout(() => {
         if (!resolved) {
            resolved = true; // Mark as resolved to prevent listener from running later
            console.warn('Segment wrapper: Osano initialization timeout (10 seconds). Aborting Segment load.');
            ctx.abort({ loadSegmentNormally: false });
            resolve(undefined); // Resolve promise to prevent blocking forever
         }
      }, 10000); // 10 second timeout
    });
  },

  /**
   * Get the current consent categories from Osano and normalize them.
   * This is called by the Segment wrapper automatically when needed.
   */
  getCategories: () => {
    if (!window.Osano.cm) {
      console.warn('Segment wrapper: Attempted to get Osano categories before SDK was ready.');
      return {};
    }
    const currentOsanoConsent = window.Osano.cm.getConsent();
    console.log('Segment wrapper getCategories - Raw Osano consent:', currentOsanoConsent);
    // Normalize the fetched consent state
    return normalizeOsanoConsent(currentOsanoConsent);
  },

  /**
   * Register a listener for Osano's consent change event ('osano-cm-consent-changed').
   * Includes debouncing to handle potentially rapid events.
   */
  registerOnConsentChanged: (setCategories) => {
    // window.CMP.onConsentChanged((event) => {
    //   setCategories(normalizeCategories(event.detail))
    // })

     // Check if Osano object exists before adding listener
     if (!window.Osano.cm) {
        console.warn('Segment wrapper: Attempted to register Osano consent listener before SDK was ready.');
        return;
     }

     let debounceTimeout; // Variable to hold the timeout ID

     window.Osano.cm.addEventListener('osano-cm-consent-changed', (change) => {
       // Clear the previous timeout if a new event comes in quickly
       clearTimeout(debounceTimeout);

       // Set a new timeout
       debounceTimeout = setTimeout(() => {
         console.log('Segment wrapper: Osano consent changed event processed (debounced):', change);
         // Fetch the *full* current state as the event only gives the delta
         const updatedOsanoConsent = window.Osano.cm.getConsent();
         console.log('Segment wrapper: Fetching full consent state after change:', updatedOsanoConsent);
				 const normalizedConsent = normalizeOsanoConsent(updatedOsanoConsent);
         // Normalize the full state and send it to Segment's wrapper
        //  setCategories(normalizeOsanoConsent(updatedOsanoConsent));
         setCategories(normalizedConsent);
       }, 300); // Debounce time in milliseconds (adjust if needed)
     });
  },
})